import React from 'react'
import { Link } from 'gatsby'
import PrevNext from '../../components/prevnext';
import '../../components/styles/kaus.less'
import Layout from '../../components/layout'
import Laptop from '../../images/macbook-kaus.svg'
import Empathy from '../../images/kaus-empathy.svg'
import Persona from '../../images/kaus-persona.svg'
import Storyboard from '../../images/kaus-story.png'
import Analysis from '../../images/kaus-analysis.svg'
import Sitemap from '../../images/kaus-sitemap.svg'
import Taskflow from '../../images/kaus-taskflow.svg'
import Userflow from '../../images/kaus-userflow.svg'
